.card {
  position: relative;
  padding-top: rem(20);
  padding-bottom: rem(35);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;

  @include tablet {
    border: none;
    display: block;
    background-color: $pale-gray;
    margin-top: rem(140);
    padding: rem(242) rem(36) rem(39);
  }
}

.card:first-of-type::before {
  @include pseudo-el {
    position: absolute;
    width: 100vw;
    height: 1px;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    background-color: $light-gray;
  }

  @include tablet {
    display: none;
  }
}

.card::after {
  @include pseudo-el {
    position: absolute;
    width: 100vw;
    height: 1px;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    background-color: $light-gray;
  }

  @include tablet {
    display: none;
  }
}

.card__pic-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: rem(28);
  flex-shrink: 0;
  flex-grow: 0;
  flex-basis: rem(84);
  height: rem(100);

  @include tablet {
    width: rem(168);
    height: rem(206);
    margin: 0;
    padding: 0;
    position: absolute;
    left: 50%;
    top: rem(-206);
    transform: translate(-50%, 50%);
  }
}

.card__pic {
  width: 100%;
}


.card__info-wrapper {
  flex-grow: 1;
  max-width: rem(320);
  min-width: rem(110);
  margin-left: rem(28);


  @include tablet {
    margin: 0;
    max-width: 100%;
  }
}

.card__heading {
  text-transform: uppercase;
  font-size: rem(16);
  line-height: calc(20 / 16);
  font-weight: bold;
  max-width: rem(100);
  margin-bottom: rem(18);

  @include tablet {
    margin: 0 auto;
    max-width: rem(130);
    font-size: rem(20);
    line-height: calc(24 / 20);
    text-align: center;
    margin-bottom: rem(20);
  }
}

.card__parametrs-wrapper {
  font-family: "Arial", "Helvetica", sans-serif;
  display: flex;
  justify-content: space-between;
  font-size: rem(12);
  margin-bottom: rem(5);

  @include tablet {
    font-size: rem(16);
    padding-bottom: rem(10);
    border-bottom: 1px solid $base-gray;
  }
}

.card__parametrs-wrapper:last-of-type {
  margin-bottom: rem(0);
}


.card__parametrs-name {
  margin-right: 1em;
}

.card__buy-link {
  font-size: rem(16);
  margin-top: rem(22);
  width: 100%;
  flex-shrink: 0;

  @include tablet {
    margin-top: rem(35);
  }
}
