.product {
  padding-top: rem(13);
  padding-bottom: rem(19);
  border-top: 1px solid $base-gray;

  @include tablet {
    padding: rem(26) rem(38) rem(28);
    border-top: 1px solid $base-gray;
    border-left: 1px solid $base-gray;
    border-right: 1px solid $base-gray;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  @include desktop {
    border-top: 1px solid $base-gray;
    border-right: none;
    border-left: none;
    align-items: center;
    padding: rem(15) 0;
    display: grid;
    grid-template-columns: repeat(14, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: rem(20);
    grid-template-areas: "n n n n n i i i i i b b b b";
  }
}

.product:last-of-type {
  border-bottom: 1px solid $base-gray;
}

.product__name {
  font-size: rem(16);
  text-transform: uppercase;
  margin-bottom: rem(13);

  @include tablet {
    font-size: rem(20);
    margin-right: rem(49);
    margin-bottom: 0;
  }

  @include desktop {
    grid-area: n;
  }
}

.product__info-wrapper {
  font-family: "Arial", "Helvetica", sans-serif;
  font-size: rem(14);
  color: $off-black;
  display: flex;
  justify-content: space-between;

  @include tablet {
    justify-content: flex-end;
  }

  @include desktop {
    grid-area: i;
  }
}


.product__desc {
  margin-right: rem(49);
  min-width: rem(100);

  @include tablet {
    margin-right: rem(114);
  }
}

.product__price {
  min-width: rem(40);
}

.product__buy-link {
  font-size: rem(16);
  margin-top: rem(10);
  padding: rem(8);

  @include tablet {
    margin-top: rem(25);
    width: 100%;
    flex-shrink: 0;
  }

  @include desktop {
    margin-top: 0;
    grid-area: b;
  }
}
