.miracles {
  width: min-content;
  margin: 0 auto;

  @include tablet {
    display: flex;
    width: rem(calc(296 * 2));
    height: rem(511);
  }
}

.miracles__pic-wrapper {
  @include tablet {
    overflow: hidden;
    width: 50%;
  }
}

.miracles__pic-wrapper--became {
  @include mobile-only {
    display: none;
  }
}

.miracles__pic-wrapper--it-was .miracles__pic {
  height: auto;

  @include tablet {
    width: rem(677);
    transform: translate(-5%);
  }
}

.miracles__pic-wrapper--it-was {
  @include mobile-only {
    width: 100%;
  }
}


.miracles__pic-wrapper--became .miracles__pic {
  width: rem(545);
  transform: translate(-48%);
}

.miracles__pic {
  height: 100%;
  max-width: none;
}
