.data {
  @include section-wrapper-b;
  padding-top: rem(27);
  padding-bottom: rem(32);

  @include tablet {
    padding-top: rem(34);
    padding-bottom: rem(72);
  }

  @include desktop {
    padding-top: rem(38);
    padding-bottom: rem(82);
  }
}

.data__info {
  text-transform: uppercase;
  line-height: calc(18 / 14);
  margin-bottom: rem(15);
  width: calc(230 / 320 * 100%);
  max-width: rem(323);

  @include tablet {
    font-size: rem(20);
    line-height: calc(30 / 20);
    margin-bottom: rem(66);
  }
}

.data__title {

  font-size: rem(36);
  font-weight: 400;
  margin-bottom: rem(37);


  @include tablet {
    font-size: rem(60);
    margin-bottom: rem(54);
  }
}
