.description {
  @include section-wrapper;
  padding-top: rem(20);
  padding-bottom: rem(40);

  @include tablet {
    padding-top: rem(30);
  }

  @include desktop {
    padding-top: rem(80);
  }
}

.description__list {
  @include reset-list-style;

  @include desktop {
    display: flex;
    flex-wrap: wrap;
    column-gap: rem(80);
    row-gap: rem(40);
  }
}

.description__item {
  margin-bottom: rem(20);

  @include tablet {
    margin-bottom: rem(30);
  }

  @include desktop {
    margin: 0;
    width: calc(570 / 1220) * 100%;
  }
}
